import React from 'react'
import './about.scss'

const About = props => (
  <div className="about">
    <h1>Our photo of sport cars</h1>
    <img src="https://s1.1zoom.ru/big0/953/Porsche_Many_514820_1280x720.jpg" alt="cars" className="about-img"/>
  </div>
)


export default About


